.student-survey-dialog {
  display: flex;
  flex-direction: column;
  min-width: 500px;
  max-width: 700px;
  max-height: 90vh;
  background: #fff;
  border-radius: 12px;
  overflow: hidden;

  .dialog-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 24px;
    background: linear-gradient(135deg, #4caf50 0%, #66bb6a 100%);
    color: white;

    .header-content {
      display: flex;
      align-items: center;
      gap: 16px;

      .header-icon {
        font-size: 32px;
        width: 32px;
        height: 32px;
      }

      .header-text {
        .dialog-title {
          margin: 0;
          font-size: 20px;
          font-weight: 600;
        }

        .dialog-subtitle {
          margin: 4px 0 0;
          font-size: 14px;
          opacity: 0.9;
        }
      }
    }

    .close-btn {
      color: white;
      
      &:hover {
        background: rgba(255, 255, 255, 0.2);
      }
    }
  }

  .dialog-content {
    flex: 1;
    overflow-y: auto;
    padding: 24px;

    .loading-state,
    .error-state {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 60px 24px;
      text-align: center;
      color: #666;

      mat-spinner {
        margin-bottom: 16px;
      }

      .error-icon {
        font-size: 64px;
        width: 64px;
        height: 64px;
        color: #ff9800;
        margin-bottom: 16px;
      }

      h3 {
        margin: 0 0 8px;
        font-size: 18px;
        color: #333;
      }

      p {
        margin: 0;
        font-size: 14px;
      }
    }

    .survey-content {
      .survey-status-section {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 24px;
        padding: 16px;
        background: #f5f5f5;
        border-radius: 8px;

        .status-badge {
          display: flex;
          align-items: center;
          gap: 8px;
          padding: 8px 16px;
          border-radius: 20px;
          font-size: 14px;
          font-weight: 500;

          mat-icon {
            font-size: 18px;
            width: 18px;
            height: 18px;
          }

          &.status-pending {
            background: #fff3e0;
            color: #f57c00;
          }

          &.status-completed,
          &.status-submitted {
            background: #e8f5e9;
            color: #4caf50;
          }
        }

        .submitted-at {
          display: flex;
          align-items: center;
          gap: 6px;
          font-size: 13px;
          color: #666;

          mat-icon {
            font-size: 16px;
            width: 16px;
            height: 16px;
          }
        }
      }

      .survey-answers {
        .answer-item {
          margin-bottom: 20px;
          padding: 16px;
          background: #fafafa;
          border-radius: 8px;
          border-left: 4px solid #4caf50;

          &.inline {
            display: flex;
            justify-content: space-between;
            align-items: center;

            .answer-header {
              margin-bottom: 0;
            }
          }

          .answer-header {
            display: flex;
            align-items: center;
            gap: 8px;
            margin-bottom: 12px;

            mat-icon {
              font-size: 20px;
              width: 20px;
              height: 20px;
              color: #4caf50;
            }

            .question {
              font-size: 14px;
              font-weight: 600;
              color: #333;
            }
          }

          .answer-content {
            font-size: 15px;
            color: #555;
            line-height: 1.6;
            white-space: pre-wrap;
            word-break: break-word;

            &.stars {
              display: flex;
              align-items: center;
              gap: 8px;

              .star-rating {
                font-size: 18px;
                color: #ffc107;
                letter-spacing: 2px;
              }

              .score-text {
                font-size: 13px;
                color: #999;
              }
            }

            .level-badge {
              display: inline-block;
              padding: 6px 14px;
              border-radius: 16px;
              font-size: 13px;
              font-weight: 500;

              &.level-beginner {
                background: #e3f2fd;
                color: #1976d2;
              }

              &.level-intermediate {
                background: #fff3e0;
                color: #f57c00;
              }

              &.level-advanced {
                background: #fce4ec;
                color: #c2185b;
              }
            }
          }
        }
      }
    }
  }

  .dialog-footer {
    padding: 16px 24px;
    border-top: 1px solid #e0e0e0;
    display: flex;
    justify-content: flex-end;
    background: #fafafa;

    button {
      min-width: 100px;
    }
  }
}
